// New server/client-profile page for user-space profile.
// This file should be created at: app/uis/user-space/profile/[id]/page.js
// It reads the route param `id`, fetches the user, and renders the profile.
// Adjust the fetch URL / API as needed for your backend.

import React from 'react';
import Link from 'next/link';

/**
 * ProfilePage
 * Route: /user-space/profile/[id]
 * Note: In Next.js app router, this receives { params } as a prop when exported as default async function.
 *
 * Example fetch: GET /api/users/:id
 * Adjust the endpoint to match your existing API.
 */
export default async function ProfilePage({ params }) {
  const { id } = params || {};

  // If your app runs server-side fetch to your API, use absolute URL or internal DB call.
  // If you prefer client-side, convert this file to 'use client' and fetch inside useEffect.
  let user = null;
  try {
    const res = await fetch(`${process.env.NEXT_PUBLIC_API_BASE || ''}/api/users/${encodeURIComponent(id)}`, {
      // make sure to allow server-side fetch (or use fetch from client if you prefer)
      cache: 'no-store',
    });
    if (res.ok) {
      user = await res.json();
    } else {
      // fallback: try by username endpoint, or return null
      user = null;
    }
  } catch (err) {
    console.error('Failed to fetch user profile:', err);
    user = null;
  }

  if (!user) {
    return (
      <div style={{ padding: 20 }}>
        <h2>User not found</h2>
        <p>Could not find profile for "{id}".</p>
        <Link href="/user-space">Back to user space</Link>
      </div>
    );
  }

  return (
    <div className="profile-page">
      <div className="profile-header">
        <img src={user.avatar || '/default-avatar.png'} alt={`${user.name || user.username} avatar`} className="profile-avatar" />
        <div>
          <h1>{user.name || user.username}</h1>
          <p className="handle">@{user.username}</p>
          <p className="bio">{user.bio}</p>
        </div>
      </div>

      <div className="profile-actions">
        {/* Optional: keep Message/conversation button here if you want */}
        <Link href={`/conversations/new?user=${encodeURIComponent(user._id || user.id || user.username)}`}>
          <button className="btn message-btn">Message</button>
        </Link>

        {/* Example: follow button or other actions */}
      </div>

      <style jsx>{`
        .profile-page { padding: 20px; max-width: 900px; margin: 0 auto; }
        .profile-header { display: flex; gap: 20px; align-items: center; }
        .profile-avatar { width: 96px; height: 96px; border-radius: 8px; object-fit: cover; }
        .handle { color: #666; margin-top: 4px; }
        .bio { margin-top: 8px; max-width: 600px; }
        .profile-actions { margin-top: 16px; display: flex; gap: 8px; }
        .btn { padding: 8px 12px; border-radius: 6px; border: none; cursor: pointer; }
        .message-btn { background: #0b84ff; color: white; }
      `}</style>
    </div>
  );
}